import React, { useState, useContext } from 'react';
import { css } from 'emotion';
import { context } from '../../context';
import useMindmap from '../../customHooks/useMindmap';
import useHistory from '../../customHooks/useHistory';
import useZoom from '../../customHooks/useZoom';
import useMove from '../../customHooks/useMove';
import * as refer from '../../statics/refer';
import * as popupType from '../../components/Popup/common/popupType';
import { handlePropagation, downloadFile } from '../../methods/assistFunctions'; //  Mindmap 
import ToolButton from '../../components/ToolButton';
import MindmapTitle from '../../components/MindmapTitle';
import Popup from '../../components/Popup';
import html2canvas from 'html2canvas';
import mindmapExporter from '../../methods/mindmapExporter';
import axios from 'axios';

const Nav = () => {
    const [popup, setPopup] = useState(popupType.NONE);
    const { mindmap: { state: mindmap }, history: { state: history }, global: { state: { title } } } = useContext(context);
    const { expandAll } = useMindmap();
    const { zoomIn, zoomOut, zoomReset } = useZoom();
    const { moveXY, moveReset } = useMove()
    const { undoHistory, redoHistory } = useHistory();

    const handleClosePopup = () => {
        setPopup(popupType.NONE);
    };

    const handleNewFile = () => {
        setPopup(popupType.NEW);
    };

    const handleDownload = () => {
        
        
        html2canvas(document.getElementById(refer.MINDMAP_ID)).then(canvas => {
            let url = canvas.toDataURL('image/png');
            downloadFile(url, `${title}.png`);
            const data = {content:mindmapExporter(mindmap, "KM")}
            alert(data)
            axios.post("http://localhost:4000/save", data).then(() => alert(`Mindmap saved`))
              .catch(err => {
                alert(err)})
        });
    };

    const handleOpenFile = () => {
        setPopup(popupType.OPEN);
    };

    const handleExport = () => {
        setPopup(popupType.EXPORT);
    };

    const handleTheme = () => {
        setPopup(popupType.THEME);
    };

    const handleUndo = () => {
        undoHistory();
    };

    const handleRedo = () => {
        redoHistory();
    };

    const handleExpand = () => {
        expandAll(refer.ROOT_NODE_ID);
    };

    const handleZoom = (zoom) => {
        console.log('', zoom ? zoom : '')
        switch (zoom) {
            case 'in':
                zoomIn()
                break;
            case 'out':
                zoomOut()
                break;
            default:
                zoomReset()
        }
    }

    const handleMove = (move) => {
        console.log('', move ? move : '')
        switch (move) {
            case 'up':
                moveXY(0,-5)
                break;
            case 'down':
                moveXY(0,5)
                break;
            case 'left':
                moveXY(-5,0)
                break;
            case 'right':
                moveXY(5,0)
                break;
            default:
                moveReset()
        }
    }

    return (<nav className={wrapper}>
        <section className={section} onClick={handlePropagation}>
            <ToolButton icon={'add-item-alt'} onClick={handleNewFile}></ToolButton>
            <ToolButton icon={'folder-open'} onClick={handleOpenFile}></ToolButton>
            <ToolButton icon={'file-download'} onClick={handleDownload}></ToolButton>
            <ToolButton icon={'duplicate'} onClick={handleExport}></ToolButton>
            <ToolButton icon={'palette'} onClick={handleTheme}></ToolButton>
            <ToolButton icon={'plus-circle'} onClick={() => handleZoom('in')}></ToolButton>
            <ToolButton icon={'minus-circle'} onClick={() => handleZoom('out')}></ToolButton>
            <ToolButton icon={'rotate-left'} onClick={() => handleZoom()}></ToolButton>
        </section>
        <section className={section}>
            <MindmapTitle />
        </section>
        <section className={section} onClick={handlePropagation}>
            <ToolButton icon={'rotate-left'} onClick={() => handleMove()}></ToolButton>
            <ToolButton icon={'arrow-left'} onClick={() => handleMove('left')}></ToolButton>
            <ToolButton icon={'arrow-up'} onClick={() => handleMove('up')}></ToolButton>
            <ToolButton icon={'arrow-down'} onClick={() => handleMove('down')}></ToolButton>
            <ToolButton icon={'arrow-right'} onClick={() => handleMove('right')}></ToolButton>
            <ToolButton icon={'undo'} disabled={history.undo.length === 0} onClick={handleUndo}></ToolButton>
            <ToolButton icon={'redo'} disabled={history.redo.length === 0} onClick={handleRedo}></ToolButton>
            <ToolButton icon={'scale'} onClick={handleExpand}></ToolButton>
        </section>
        {popup !== popupType.NONE &&
            <Popup type={popup} handleClosePopup={handleClosePopup} handleDownload={handleDownload} />}
    </nav>);
};

export default Nav;

// CSS
const wrapper = css`
display: flex;
justify-content: space-between;
position: fixed;
top:0;
left:0;
right:0;
height: 56px;
padding: 0 50px;
font-size: 25px;
background-color: #ffffff;
box-shadow: 0 0px 2px #aaaaaa;
z-index: 10;
`;

const section = css`
display: flex;
`;